*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img{
  max-width: 100%;
}
ul{
  list-style-type:none ;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

}

.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px; 
   background-color: #1e427c;
}

.header .links{
  margin: 0 10px;
  text-decoration: none;
  color: #fff;
}

.showcase{
  background: url('./assets/1.jpg') no-repeat center/cover;
  height: 650px;

}
.showcase .overplay{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 650px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.4);
}

.showcase .overplay h1{
  font-size: 36px;
  padding-bottom:20px ;
}


.grid{
 padding: 50px;

}

.grid-h3{
  text-align: center;
  font-size: 30px;
  color: #333;
  padding-bottom: 20px;
}

.grid .grid-items div{
  background-color: #f1f1f1;
  margin: 20px 0;
  padding: 20px;
  border-radius: 10px;
}
.grid .grid-items div img{
  border-radius: 10px;
}
.grid .grid-items div h4{
  padding: 20px 0;
  font-size: 22px;
}
.grid .grid-items div p{
  line-height: 1.8;
}
.footer{
  background-color: #333;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer ul li{
  margin:10px 0;
}
.footer ul li button{
  background-color:transparent;
  color:white;
  padding:8px;
  border:none;
  font-family:inherit;
  letter-spacing:1px;
  transition: background-color 0.4s ease;
  border-radius:5px;
  cursor:pointer;
}

.footer ul li button:hover{
  background-color: #1e427c;
  cursor: pointer;

}


/* login */

.login .form{
  display: flex;
  flex-direction: column;
}
.login .form input{
  margin: 8px 0;
  width: 80vw;
  padding: 12px;
  border-radius: 10px;
  font-family: inherit;
  font-weight: 700;
  letter-spacing: 2px;
  color: #000;

}
.login .form input::placeholder{
  font-weight: 800;
  color: #000;
}

.login .form button{
  background-color: #1e427c;
  color: white;
  padding: 8px;
  border: 2px solid #1e427c;
  font-family: inherit;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 10px;
  transition: background-color 0.4s ;
  letter-spacing: 2px;
}
.login .form button:hover{
  cursor: pointer;
  background-color:transparent;
}

@media (min-width:768px){
  .showcase .overplay h1{
    font-size: 64px;
  }
  .showcase .overplay p{
    font-size: 22px;
  }
  .grid .grid-items{
    display:flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    align-items: center;

  }
  .grid .grid-items div img{
    height: 150px;
    width: 300px;
  }
}
@media (min-width:992px){
  .showcase {
    background-position: top;
  }
  .grid {
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;

  }
  .grid .grid-items{
    display:flex;
    flex-wrap: wrap;
    text-align: center;
    flex-direction:row;
    width: 30%;
   

  }
  .login .form input{
    width: 50vw;
  }
}


